
.ms-date-range-field{
    .ms-calendar{
        cursor: pointer;
    }
    .ms-picker{
        position:relative;
        button{
            outline: none;
        }
        .ms-picker-box{
            position: absolute;
            display: inline-block;
            padding: 9px;
            background-color: #f5f5f5;
            border: 1px solid #e3e3e3;
            border-radius: 4px;
            box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.05);
            z-index: 200;
            .ms-datepicker-table{
                display: table;
                .ms-datepicker-tr{
                    display: table-row;
                    .ms-datepicker-td{
                        display: table-cell;
                        vertical-align: top;
                        &.ms-btn-previous{
                            vertical-align: top;
                            button{
                                box-sizing: initial;
                                height:100%;
                            }
                        }
                        &.ms-btn-next{
                            vertical-align: top;
                            button{
                                box-sizing: initial;
                                height:100%;
                            }
                        }
                    }
                }
            }
            .ms-span-right{
                float: right;
            }
        }
        .ms-picker-box-up{
            bottom: 0px;
        }
        .ms-picker-box-down{
            top:34px;
        }
    }
}



