
.ms-datepicker-range{
    display: inline-block;
    tbody{
        tr{
            td{
                button{
                    &.ms-today{color: #00b3ee;}
                    &.other-mouth{color: #777;}
                    &.select-day{
                         color: #fff;
                         background-color: #31b0d5;
                         border-color: #269abc;
                     }
                }
            }
        }
    }
    button{outline: none;}
    .ms-weekday-line{
        th{
            text-align: center;
        }
    }
    .ms-center{
        text-align: center;
        width: 100%;
    }
}



