
.grid-panel{
    width: 100%;
    position: relative;
    .grid-header{
        &.scroll-show{
             padding-right: 14px;
         }
        .table-bordered{border: 0px;}
        .table{
            margin-bottom: 0px;
            thead{
                tr{
                    th{
                        min-height: 47px;
                        border-bottom: 0px;
                    }
                }
            }
        }
    }
    .grid-header-line{
        border-bottom: 2px solid #ddd;
    }
    .grid-body-box{
        position: relative;
        .ms-grid-loading{
            position: absolute;
            top: 0px;
            bottom: 0px;
            left: 0px;
            right: 0px;
        }
        .grid-body{
            position: relative;
            overflow-y: auto;
            .table{
                margin-bottom: 0px;
                tr{min-height: 47px;}
            }
        }
    }

    .grid-footer{

    }
}



