
.ms-date-range-single-field{
    .ms-calendar{
        cursor: pointer;
    }
    .ms-picker{
        position:relative;
        .ms-picker-box{
            position: absolute;
            display: inline-block;
            padding: 9px;
            background-color: #f5f5f5;
            border: 1px solid #e3e3e3;
            border-radius: 4px;
            -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.05);
            box-shadow: inset 0 1px 1px rgba(0,0,0,.05);
            z-index: 200;

            .ms-span-right{
                float: right;
            }
        }
        .ms-picker-box-up{
            bottom: 0px;
        }
        .ms-picker-box-down{
            top:34px;
        }
    }
}



