
.ms-tree-view{
    ul{
        list-style-type: none;padding: 0px;margin: 0px;
        li{
            line-height: 22px; cursor: pointer;
            &:hover{background: rgba(136, 200, 255, 0.5);}
            &.active{background: rgba(136, 200, 255, 0.5);}
        }
    }
}



