
.ms-grid-action-item{
    display: inline-block;
    margin: 3px;
    cursor: pointer;
    .ms-grid-action-item-tooltip-box{
        position: relative;
        .ms-grid-action-item-tooltip{
            position:fixed;
            border: 1px rgba(0, 0, 0, 0.3) solid;
            border-radius: 4px;
            padding: 5px;
            background: #868686;
            z-index: 1;
            color: white;
        }
    }
    .ms-grid-action-item-disabled{
        opacity: 0.3;
        cursor: default;
        pointer-events: none;
    }
}




