    line-height: 22px;
    word-wrap: break-word;
    &.tree-panel-first-level{

    }
    &.tree-panel{
         margin-left:18px;
     }
     li{
        div{
            &.ms-tree-panel-checkbox{
                 margin-top: 0px!important;
