
.ms-paging{
    border: 1px #ddd solid;
    line-height: 34px;
    .glyphicon{
        cursor: pointer;
    }
    .tbspacer{
        width: 5px;
        display: inline-block;
    }
    .tbseparator{
        display: inline-block;
        height: 18px;
        border-left: 1px #989898 solid;
        vertical-align: middle;
    }
    .ms-paging-num{
        width: 40px;
        height: 30px;
        vertical-align: middle;
        line-height: 30px;
        font-size: 14px;
        padding-left: 5px;
        outline: none;
    }
    .ms-paging-left{
        display: inline-block;
    }
    .ms-paging-right{
        display: inline-block;
        float: right;
        padding-right: 10px;
    }
    .ms-paging-disabled{
        opacity: 0.3;
        pointer-events: none;
    }
}



